import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter, HashRouter } from 'react-router-dom';
import './index.css';
import { App } from './components/app';

import './style.css';
import { Provider } from 'react-redux'; // store   
import store, { persistor } from './storage/store'; // 
import { PersistGate } from 'redux-persist/integration/react';



const root = ReactDOM.createRoot(document.getElementById('root'));
//        
const Router = process.env.REACT_APP_GH_PAGES !== 'true' ? BrowserRouter : HashRouter;
//process.env   -     
//HashRouter       , ..   BrowserRouter
//   package.json     "deploy-gh": "cross-env REACT_APP_GH_PAGES=true npm run build && gh-pages -d build"
//  "homepage": "./",        index.html    


root.render(
  <Provider store={store}>
    <PersistGate persistor={persistor}>
      <Router>
        <App />
      </Router>
    </PersistGate>
  </Provider>

);

