
import { useForm } from 'react-hook-form';
import s from './styles.module.css'
import classNames from 'classnames';
import Form from '../form';
import FormInput from '../form-input';
import FormButton from '../form-button';
import { FormEvent } from 'react';

type TFormValues = {
    email: string;    
}

interface IResetPasswordFormProps {
    onSubmit: (dataForm: {email:string}) => void, 
    onNavigate: (value: string ) => void
}

function ResetPasswordForm({onSubmit, onNavigate}: IResetPasswordFormProps) {

    const {register, handleSubmit, formState: {errors}} = useForm<TFormValues>({mode: 'onBlur'});
    

    const emailRegister = register('email', {
        required: {
            value: true,
            message: " "
        },
        pattern: {
            value: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            message: "Email     "
        }
    })

    
    return ( 
        <>
        <Form title={' '} handleForm={handleSubmit(onSubmit)}>
            <FormInput 
                {...emailRegister}
                id='email'
                type='text'
                placeholder='email'
            />
            {errors?.email && <p className='errorMessage'>{errors.email.message}</p>}            
            <p className='infoText'>    24 .</p>
            <FormButton type='submit' color='primary' extraClass={s.formButton}></FormButton>
            <FormButton onClick={() => onNavigate('/')} type='button' color='secondary' extraClass={s.formButton}> </FormButton>
        </Form>

        </>
        
     );
}

export default ResetPasswordForm;