
import { useForm } from 'react-hook-form';
import s from './styles.module.css'
import Form from '../form';
import FormInput from '../form-input';
import FormButton from '../form-button';
import { UserRegisterBodyDto } from '../../utils/api';

 

interface IRegisterFormProps {
    onSubmit:  (dataform: UserRegisterBodyDto) => void;
    onNavigate: (value: string) => void;
}

function RegisterForm({onSubmit, onNavigate}: IRegisterFormProps) {

    const {register, handleSubmit, formState: {errors}} = useForm<UserRegisterBodyDto>({mode: 'onBlur'});
    
/*     const navigate = useNavigate();
    const location = useLocation();
    const initialPath = location.state?.initialPath; */

    //    



    const emailRegister = register('email', {
        required: {
            value: true,
            message: " "
        },
        pattern: {
            value: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            message: "Email     "
        }
    })

    const passwordRegister = register('password', {
        required: {
            value: true,
            message: " "
        },
        pattern: {
            value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
            message: "     ,       " 
        }
    })

    const groupRegister = register('group', {
        required: {
            value: true,
            message: " "
        },
        pattern: {
            value: /^group-[0-9]{1,3}$/,
            message: "    group-" 
        }
    })

        
    return ( 
        <>
        <Form title={''} handleForm={handleSubmit(onSubmit)}>
            <FormInput 
                {...emailRegister}
                id='email'
                type='text'
                placeholder='email'
            />
            {errors?.email && <p className='errorMessage'>{errors.email.message}</p>}
            <FormInput 
                {...groupRegister}
                id='group'
                type='text'
                placeholder=' '
            />
            {errors?.group && <p className='errorMessage'>{errors.group.message}</p>}
            <FormInput 
                {...passwordRegister}
                id='password'
                type='password'
                placeholder=''
            />
            {errors?.password && <p className='errorMessage'>{errors.password.message}</p>}
            <p className='infoText'>  ,             .</p>
            <FormButton type='submit' color='primary' extraClass={s.formButton}></FormButton>
            <FormButton onClick={() => onNavigate('/login')} type='button' color='secondary' extraClass={s.formButton}></FormButton>
        </Form>
        
        
        
        
        
        
        
        
        
        
        {/* <form onSubmit={handleSubmit(handleSubmitForm)} noValidate>
            <h3></h3>
            <input 
                {...register('name', {
                    required: {
                        value: true,
                        message: ' '
                    },
                    min: {
                        value: 3,
                        message: '  3'
                    }
                })}
                type="text"                 
                placeholder="" 

            />
            {errors.name && <p>{errors.name.message}</p>}
            <input 
                {...emailRegister}
                type="email"                 
                placeholder="email" 
                autoComplete='none'

            />
            {errors.email && <p>{errors.email.message}</p>}
            <input
                {...register('password', {
                    required: true,
                    pattern: {
                        value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
                        message: "     ,       " 
                    }
                })} 
                type="password"                 
                placeholder=" "
                autoComplete='none' 

            />
            {errors.password && <p>{errors.password.message}</p>}
            <button></button>
        </form> */}
        </>
        
     );
}

export default RegisterForm;